  .load-more {
    display: block;
    margin: 0 auto;
  }
  
  .load-more.is-hidden {
    display: none;
  }
  
  .search-btn:hover,
  .search-btn:focus,
  .load-more:hover,
  .load-more:focus {
    background-color: #27ae60;
  }
  
  .gallery {
    display: flex;
    flex-wrap: wrap;
    margin-left: -30px;
    margin-top: -30px;
    margin-bottom: 20px;
  }
  
  .gallery__item {
    flex-basis: calc((100% - 120px) / 4);
    margin-left: 30px;
    margin-top: 30px;
    height: 272px;
  }
  
  .gallery-img {
    width: 100%;
    height: 100%;
    max-width: unset;
    object-fit: cover;
  }
  .posts-section {
    padding: 100px 0;
  }
  
  .posts {
    display: flex;
    flex-wrap: wrap;
    gap: 30px 5%;
    margin-bottom: 40px;
  }
  
  .posts__item {
    flex-basis: 30%;
    height: 340px;
    padding: 20px;
    box-shadow: 0 0 10px 0 #cccccc;
    overflow-y: auto;
  }
  
  .posts__title {
    font-size: 22px;
    margin-bottom: 20px;
  }
  
  .posts__text {
    font-size: 18px;
    margin-bottom: 10px;
    line-height: 1.4;
  }
  
  .load__more__btn {
    display: block;
    margin: 0 auto;
    min-width: 200px;
    padding: 14px 4px;
    background-color: #34495e;
    color: #ffffff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 250ms linear;
  }
  
  .load__more__btn.is-hidden {
    display: none;
  }
  
  .load-more-btn:hover,
  .load-more-btn:focus {
    background-color: #2c3e50;
  }